"use client"

import { useState } from "react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"

export default function ContactPage() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    company: "",
    subject: "",
    message: ""
  })
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitStatus, setSubmitStatus] = useState<"idle" | "success" | "error">("idle")

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)
    setSubmitStatus("idle")

    try {
      const response = await fetch("/api/send-email", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      })

      if (response.ok) {
        setSubmitStatus("success")
        setFormData({
          name: "",
          email: "",
          company: "",
          subject: "",
          message: ""
        })
      } else {
        setSubmitStatus("error")
      }
    } catch (error) {
      console.error("Error submitting form:", error)
      setSubmitStatus("error")
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <div className="bg-white">
      {/* Hero Section */}
      <section className="bg-gradient-to-br from-gray-50 to-white py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-6">
              Get in Touch
            </h1>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Ready to strengthen your cybersecurity posture? Contact our experts to discuss 
              your security needs and discover how we can help protect your organization.
            </p>
          </div>
        </div>
      </section>

      {/* Contact Form Section */}
      <section className="py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
            {/* Contact Form */}
            <div>
              <h2 className="text-3xl font-bold text-gray-900 mb-8">Send Us a Message</h2>
              
              {submitStatus === "success" && (
                <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-lg">
                  <p className="text-green-800">
                    Thank you for your message! We&apos;ll get back to you within 24 hours.
                  </p>
                </div>
              )}

              {submitStatus === "error" && (
                <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
                  <p className="text-red-800">
                    There was an error sending your message. Please try again or contact us directly.
                  </p>
                </div>
              )}

              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <Label htmlFor="name">Full Name *</Label>
                    <Input
                      id="name"
                      name="name"
                      type="text"
                      required
                      value={formData.name}
                      onChange={handleInputChange}
                      className="mt-1"
                      placeholder="Your full name"
                    />
                  </div>
                  <div>
                    <Label htmlFor="email">Email Address *</Label>
                    <Input
                      id="email"
                      name="email"
                      type="email"
                      required
                      value={formData.email}
                      onChange={handleInputChange}
                      className="mt-1"
                      placeholder="your.email@company.com"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="company">Company/Organization</Label>
                  <Input
                    id="company"
                    name="company"
                    type="text"
                    value={formData.company}
                    onChange={handleInputChange}
                    className="mt-1"
                    placeholder="Your company name"
                  />
                </div>

                <div>
                  <Label htmlFor="subject">Subject *</Label>
                  <Input
                    id="subject"
                    name="subject"
                    type="text"
                    required
                    value={formData.subject}
                    onChange={handleInputChange}
                    className="mt-1"
                    placeholder="What can we help you with?"
                  />
                </div>

                <div>
                  <Label htmlFor="message">Message *</Label>
                  <Textarea
                    id="message"
                    name="message"
                    required
                    value={formData.message}
                    onChange={handleInputChange}
                    className="mt-1"
                    rows={6}
                    placeholder="Tell us about your security needs, challenges, or questions..."
                  />
                </div>

                <Button
                  type="submit"
                  disabled={isSubmitting}
                  className="w-full text-lg py-3"
                >
                  {isSubmitting ? "Sending..." : "Send Message"}
                </Button>
              </form>
            </div>

            {/* Contact Information */}
            <div>
              <h2 className="text-3xl font-bold text-gray-900 mb-8">Contact Information</h2>
              
              <div className="space-y-8">
                <div>
                  <h3 className="text-xl font-semibold text-gray-900 mb-4">Get Started Today</h3>
                  <p className="text-gray-600 mb-4">
                    Our security experts are ready to help you assess your current posture, 
                    identify vulnerabilities, and implement comprehensive protection strategies.
                  </p>
                  <ul className="text-gray-600 space-y-2">
                    <li>• Free initial consultation</li>
                    <li>• Rapid response within 24 hours</li>
                    <li>• Customized security solutions</li>
                    <li>• Industry-leading expertise</li>
                  </ul>
                </div>

                <div>
                  <h3 className="text-xl font-semibold text-gray-900 mb-4">Response Time</h3>
                  <div className="bg-gray-50 rounded-lg p-6">
                    <div className="flex justify-between items-center mb-2">
                      <span className="text-gray-600">General Inquiries</span>
                      <span className="font-semibold text-gray-900">24 hours</span>
                    </div>
                    <div className="flex justify-between items-center mb-2">
                      <span className="text-gray-600">Security Emergencies</span>
                      <span className="font-semibold text-gray-900">2 hours</span>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-gray-600">Partnership Inquiries</span>
                      <span className="font-semibold text-gray-900">48 hours</span>
                    </div>
                  </div>
                </div>

                <div>
                  <h3 className="text-xl font-semibold text-gray-900 mb-4">Prefer to Schedule?</h3>
                  <p className="text-gray-600 mb-4">
                    Book a consultation directly with one of our security experts to discuss 
                    your specific needs and explore how we can help.
                  </p>
                  <Button asChild variant="outline" className="w-full">
                    <Link href="/schedule">Schedule a Consultation</Link>
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Services Quick Links */}
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              Interested in Specific Services?
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Jump directly to learn more about our specialized security services.
            </p>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            <Link
              href="/services/security"
              className="bg-white rounded-lg p-6 text-center hover:shadow-lg transition-shadow group"
            >
              <div className="w-12 h-12 bg-black rounded-lg flex items-center justify-center mx-auto mb-4 group-hover:bg-gray-800 transition-colors">
                <div className="w-6 h-6 bg-white rounded-sm"></div>
              </div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">In-House Security</h3>
              <p className="text-gray-600 text-sm">
                Dedicated security professionals working within your organization.
              </p>
            </Link>

            <Link
              href="/services/solutions"
              className="bg-white rounded-lg p-6 text-center hover:shadow-lg transition-shadow group"
            >
              <div className="w-12 h-12 bg-black rounded-lg flex items-center justify-center mx-auto mb-4 group-hover:bg-gray-800 transition-colors">
                <div className="w-6 h-6 border-2 border-white rounded-sm"></div>
              </div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Full Solutions</h3>
              <p className="text-gray-600 text-sm">
                Complete end-to-end security solutions and implementation.
              </p>
            </Link>

            <Link
              href="/services/training"
              className="bg-white rounded-lg p-6 text-center hover:shadow-lg transition-shadow group"
            >
              <div className="w-12 h-12 bg-black rounded-lg flex items-center justify-center mx-auto mb-4 group-hover:bg-gray-800 transition-colors">
                <div className="w-5 h-5 bg-white rounded-full"></div>
              </div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Training Programs</h3>
              <p className="text-gray-600 text-sm">
                Comprehensive cybersecurity education and awareness training.
              </p>
            </Link>

            <Link
              href="/services/consultants"
              className="bg-white rounded-lg p-6 text-center hover:shadow-lg transition-shadow group"
            >
              <div className="w-12 h-12 bg-black rounded-lg flex items-center justify-center mx-auto mb-4 group-hover:bg-gray-800 transition-colors">
                <div className="w-6 h-2 bg-white rounded-full"></div>
              </div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Expert Consultants</h3>
              <p className="text-gray-600 text-sm">
                Placement of experienced security consultants in your organization.
              </p>
            </Link>
          </div>
        </div>
      </section>

      {/* FAQ Section */}
      <section className="py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              Frequently Asked Questions
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Common questions about our services and how we can help secure your organization.
            </p>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            <div>
              <h3 className="text-lg font-semibold text-gray-900 mb-3">
                How quickly can you respond to security emergencies?
              </h3>
              <p className="text-gray-600 mb-6">
                We provide emergency response within 2 hours for critical security incidents. 
                Our team is available 24/7 to handle urgent security matters and can deploy 
                resources immediately when needed.
              </p>
            </div>
            <div>
              <h3 className="text-lg font-semibold text-gray-900 mb-3">
                Do you work with small and medium-sized businesses?
              </h3>
              <p className="text-gray-600 mb-6">
                Absolutely! We provide scalable security solutions for organizations of all sizes. 
                Our services are designed to be cost-effective and appropriate for SMBs while 
                delivering enterprise-grade protection.
              </p>
            </div>
            <div>
              <h3 className="text-lg font-semibold text-gray-900 mb-3">
                What industries do you specialize in?
              </h3>
              <p className="text-gray-600 mb-6">
                We serve all industries but have particular expertise in healthcare, financial 
                services, manufacturing, government, education, and retail. Our consultants 
                understand industry-specific compliance requirements and threats.
              </p>
            </div>
            <div>
              <h3 className="text-lg font-semibold text-gray-900 mb-3">
                Can you help with compliance requirements?
              </h3>
              <p className="text-gray-600 mb-6">
                Yes, we specialize in helping organizations achieve and maintain compliance 
                with various regulations including HIPAA, PCI DSS, SOX, FISMA, and others. 
                Our experts can guide you through the entire compliance process.
              </p>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
